h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

*, :before, :after {
  box-sizing: border-box;
}

a, a:link, a:visited {
  text-decoration: none;
}

:root {
  --primary-text-color: #f8f8f8;
  --secondary-text-color: #b7b7b7;
  --primary-title-color: #fff;
  --primary-bgc: #282828;
  --primary-btn-color: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --primary-ligth-theme-text-color: #282828;
  --secondary-ligth-theme-text-color: #595959;
  --ligth-theme-title-color: #111;
  --ligth-theme-bgs: #fff;
  --primary-ligth-theme-btn-color: linear-gradient( 141.22deg, #ffc226 9.4%, #f84119 91.91% );
  --accent-color: #f87719;
  --black: #000;
  --white: #fff;
}

body {
  color: var(--primary-text-color);
  background-color: var(--primary-bgc);
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

.container {
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}





.search-form {
  justify-content: center;
  align-items: center;
  gap: 16px;
  display: flex;
}

.search-form-input {
  width: 173px;
  height: 40px;
  color: #595959;
  border: 1px solid #595959;
  border-radius: 8px;
  padding: 12px 20px;
  font-size: 14px;
  font-weight: 500;
  line-height: 16px;
}

.search-btn {
  width: 40px;
  height: 40px;
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border-radius: 50%;
  padding: 11px;
}

.search-icon {
  width: 18px;
  height: 18px;
  fill: currentColor;
}





